/* EventDetails.css */



/* Styling for modal overlay */
.event-details-overlay {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0; /* Start with opacity 0 */
  transition: opacity 0.3s ease; /* Transition effect for opacity */
}

/* Show modal overlay when showModal is true */
.event-details-overlay.active {
  opacity: 1; /* Fade in the modal overlay */
}

/* Styling for modal content */
.event-details-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transform: scale(0.8); /* Start with smaller size */
  transition: transform 0.3s ease; /* Transition effect for scale */
}

/* Show modal content when showModal is true */
.event-details-content.active {
  transform: scale(1); /* Scale up the modal content */
}



.modul-event-name {
  text-align: center;
  align-items: center;
  padding-top: 15px;
  margin: 0;
  padding-bottom: 0;
}
.p-info {
  text-align: left; /* Align text to the left */
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 0;
  padding-bottom: 0;
  margin: 0;
}


.close-button {
  position: absolute;
  top: -20px; /* Position the button above the modal window */
  right: -20px; /* Position the button outside the modal window */
  width: 35px; /* Adjust the width and height to make the button circular */
  height: 35px;
  border-radius: 50%; /* Make the button circular */
  background-color: rgba(170, 170, 170, 0.5); /* Semi-transparent background color */
  border: 2px solid rgb(167, 195, 255); /* Blue border with 2px size */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px; /* Adjust font size as needed */
  color: rgb(167, 195, 255); /* Text color */
  cursor: pointer;
  z-index: 999; /* Ensure the button is above other elements */
}

.close-button:hover {
  color: white; /* Text color on hover */
  border-color: white; /* Border color on hover */
}

.spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999; /* Set a higher z-index to make sure it's above other elements */
}
